@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&display=swap);
*{
  box-sizing : border-box;
  padding : 0;
  margin: 0;
}

a{
  text-decoration: none;
}

body {
  margin: 0;
  font-family: 'Baloo Tamma 2', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#121212;
  color : #FFFFF6;
  max-width: 90vw;
  margin: 0 auto;
}

a{
  color : rgb(124, 119, 119);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding : 2rem;
  text-align: left;
}


.match__detail__won__card{
    display: flex;
    justify-content: space-between;
    /* max-width: 80vw; */
    align-items: center;
    margin-top: 5vh;
    margin-bottom: 5vh;
    background-color: #4da375;
    padding : 0.75rem;
}

.match__detail__lost__card{
    display: flex;
    justify-content: space-between;
    /* max-width: 80vw; */
    align-items: center;
    margin-top: 5vh;
    margin-bottom: 5vh;
    background-color: #a34d5d;
    padding : 0.75rem;
    
}

.match__date , .match__venue, .match__result{
    /* margin-top : 3vh; */
    font-size: 1.5rem;
    color: white;


}

.latest__matches{
    display:flex;
    flex-direction: column;
    grid-row-gap: 0;
    row-gap: 0;
    padding: 0;
}

.head{
    font-size: 2rem;
    position: absolute;
}

.noraml{
    font-size: 1.5rem;
}

.link1{
    font-size : 2.5rem;
    color: black;
    /* height: 50px; */
}

.add__detail{
    display: flex;
    flex-direction: column;
    /* width:40vw; */
    grid-row-gap: 1rem;
    row-gap: 1rem;
    /* margin-top: 5vh; */
}

.first{
    display: flex;
    flex-direction: column;
    /* margin-left: 0; */
    /* column-gap: 4.4rem; */
}
.second{
    display: flex;
    flex-direction: column;
    /* margin-left: 0; */
    /* column-gap: 3rem; */
}

.mom{
    display: flex;
    flex-direction: column;
    /* column-gap: 1.8rem; */
}

.umpire{
    display:flex;
    flex-direction: column;
    /* column-gap: 6.35rem; */
}

.value {
    color: white;
    font-size: 1.5rem;
}

.line2{
    display: flex;
    align-items: baseline;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}
.normal{
    font-size: 1rem;
}
.link{
    font-size : 1.7rem;
    color: black;
}

.match__result{
    font-size : 1.3rem
}

.match__small__won__card{
    background-color: #4da375;
    padding-left : 2rem;
    padding-right: 2rem;
    padding-top:0.4rem;
    padding-bottom : 0.4rem;
}

.match__small__lost__card{
    background-color: #a34d5d;
    padding-left : 2rem;
    padding-right: 2rem;
    padding-top:0.4rem;
    padding-bottom : 0.4rem;
}
.Team__Page{
    display: flex;
    flex-direction: column;
    /* column-gap:1rem;
    row-gap: 1rem; */
    
}

.team__current{
    display: flex;
    justify-content: space-between;
    /* height : 17vh; */
    /* margin-bottom: 2vw; */
}

.team__name{
    margin-top:2vh ;
    font-size :3.5rem;
}

.win__loss{
    height:15vh;
    margin-top: 2vh;
    margin-right : 5vw;
}
.team__previous{
    display: flex;
    justify-content: space-between;
}

.win__loss{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

.wins{
    align-self: center;
    justify-content: center;
    font-size: 1.3rem;

}

.more__link{
    align-self: center;
    justify-content: center;
    font-size: 1.5rem;
}

.row{
    display:flex;
    flex-direction: column;
}
.match__page{
    /* display: grid;
    grid-template-columns: 1fr 1fr; */

    display: flex;
    justify-content: space-evenly;
}

.year__selector{
    /* position: absolute; */
    /* justify-self: center; */
    margin-top: 10vh;
}
.team__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 1rem;
    grid-row-gap : 1rem;

    justify-self: center;
    align-items: center;
    text-align: center;
}


.team__tile{
    height : 15vh;
    text-align: center;
    border : 1px solid gold;
    padding: 2rem;
}
